import axios from '../config/api.js'//axios
import URL from './url.js'//URL
import global from './global.js'
import $tool from './tool.js'


//
const data = {
  categoryData:'',
  area: '',
  scale: '',
  stage: '',
  industry: '',
  hotCity:'',
  entrance:'index',// 
  user_id:'',//id
  id:'',//id
}


//
var func = {
  getCity(data) {
    let arr = [];
    data.forEach((x)=>{
      let obj = {};
      obj.label = x.area_title;
      obj.value = x.area_id;
      arr.push(obj);
    });
    return arr
  },//
  getScale(data) {
    let arr = [];
    data.forEach((x)=>{
      let obj = {};
      obj.label = x.scale_money;
      obj.value = x.scale_id;
      arr.push(obj)
    });
    return arr
  },//
  getStage(data) {
    let arr = [];
    data.forEach((x)=>{
      let obj = {};
      obj.label = x.stage_name;
      obj.value = x.stage_id;
      arr.push(obj)
    });
    return arr
  },//
  getIndustry(data) {
    let arr = [];
    data.forEach((x)=>{
      let obj = {};
      obj.label = x.industry_name;
      obj.value = x.industry_id;
      arr.push(obj)
    });
    return arr
  },//
  getTags_pro(data){
    let arr = [];
    data.forEach((x)=>{
      let obj = {};
      obj.label = x.tag_name;
      obj.value = x.tag_id;
      arr.push(obj)
    });
    return arr
  },////
  getResource(data){
    let arr = [];
    data.forEach((x)=>{
      let obj = {};
      obj.label = x.resource_name;
      obj.value = x.resource_id;
      arr.push(obj)
    });
    return arr
  },//
  getCompanyStatus(data){
    let arr = [];
    for (let i = 0; i < data.length; i++) {
      let obj = {};
      obj.label = data[i].status_id;
      obj.value = data[i].status_name;
      arr.push(obj)
    }
    arr.push({label: '', value: ''})
    return arr
  },//
  getCompany_scale(data){
    let arr = [];
    for (let i = 0; i < data.length; i++) {
      let obj = {};
      obj.label = data[i].comp_scale_value;
      obj.value = data[i].comp_scale_id;
      arr.push(obj);
    }
    return arr
  },//
  getWxProjectCategory() {
    axios.post(URL.getWxProjectCategory, {user_id: localStorage.user_id})
    .then(res => {
      let data = res.data.data;
      global.data.categoryData=data;
      global.data.area = global.func.getCity(data.area);//1
      global.data.scale = global.func.getScale(data.scale);//
      global.data.stage = global.func.getStage(data.stage);//
      global.data.industry = global.func.getIndustry(data.industry);//
      global.data.hotCity = global.func.getCity(data.hotCity);//
      global.data.tags_pro = global.func.getTags_pro(data.tags_pro);//
      global.data.tags_team = global.func.getTags_pro(data.tags_team);//
      global.data.tags_user = global.func.getTags_pro(data.tags_user);//
      global.data.pro_source = global.func.getTags_pro(data.pro_source);//
      global.data.resource = global.func.getResource(data.resource);//
      global.data.company_status = global.func.getCompanyStatus(data.company_status);//
      global.data.company_scale = global.func.getCompany_scale(data.company_scale);//
    })
    .catch(err => {
      console.log(err);
      console.log('')
    })
  },//

}


//
func.getWxProjectCategory();

export default {
  data, func
}



