import { Notification } from 'element-ui';
const tool={
/**/
  getToObject (data) {
    let object={};
    for(let key in data){
      object[key]=data[key]
    }
    return object;
  }, // 
  getToArrObject(data) {
    let newArr = new Array;
    for(let i=0;i<data.length; i++){
      newArr[i]=tool.getToObject(data[i])
    }
    return newArr;
  },//*

  fileChange(target) {
  let isIE = /msie/i.test(navigator.userAgent) && !window.opera;
  let fileSize = 0;
  const filetypes =[".jpg","why.png",".rar",".txt",".zip",".doc",".ppt",".xls",".pdf",".docx",".xlsx"];
  const filepath = target.value;
  const filemaxsize = 1024*2;//2M
  if(filepath){
    var isnext = false;
    var fileend = filepath.substring(filepath.indexOf("."));
    if(filetypes && filetypes.length>0){
      for(var i =0; i<filetypes.length;i++){
        if(filetypes[i]==fileend){
          isnext = true;
          break;
        }
      }
    }
    if(!isnext){
      alert("");
      target.value ="";
      return false;
    }
  }else{
    return false;
  }
  if (isIE && !target.files) {
    var filePath = target.value;
    var fileSystem = new ActiveXObject("Scripting.FileSystemObject");
    if(!fileSystem.FileExists(filePath)){
      alert("");
      return false;
    }
    var file = fileSystem.GetFile (filePath);
    fileSize = file.Size;
  } else {
    fileSize = target.files[0].size;
  }

  var size = fileSize / 1024;
  if(size>filemaxsize){
    alert(""+filemaxsize/1024+"M");
    target.value ="";
    return false;
  }
  if(size<=0){
    alert("0M");
    target.value ="";
    return false;
  }
},/*fileChange(this)*/

  getTitleSift(data){
    let arr = [];
    for(let key in data){
      let obj=new Object;
      obj.text=data[key]
      obj.value=key
      arr.push(obj)
    }
    return arr
  },//======
  checkArr(arr, arr2){
    let newArr = []
    let data = arr[arr.length - 1]
    for (let i = 0; i < arr2.length; i++) {
      newArr.push(arr2[i].value);
    }
    if (newArr.indexOf(data) == -1) return data


  },//.
  setTag(arr,pro){
    for(let i=0; i<pro.length; i++){
      for(let j=0; j<arr.length; j++){
        if(arr[j]==pro[i].label){
          arr[j]=pro[i].value;
        }
      }
    }
  },//arr//,pro/,id
/**/
  getNull(data) {
    let reg=/\S/;
    if (!reg.test(data))
    {
      return true;//
    }else{
      return false;//
    }

  },//()
  checkNumber(data) {
    let reg = /^[0-9]+.?[0-9]*$/;
    if (reg.test(data)) {
      return true;
    }
    return false;
  },//
  checkEmail(data){
    let reg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
    if (reg.test(data)) {
      return true;
    }
    return false;
  },//
  checkPhoneNubmer(data){
    let reg = /^1(3|4|5|7|8)\d{9}$/;
    if (reg.test(data)) {
      return true;
    }
    return false;
  },//
  checkPassword(data){
    if (data.length>20 || data.length<6){
      return true
    }
  },//(6-20)
  checkLength20(data){
    if(typeof data ==='array'){
      data.forEach(x=>{
        if(x.length>20){
          return true
        }
      })
      return false
    }else{
      if(data.length>20){
        return true
      }else{
        return false
      }
    }
  },//input(20true)
  checkLength40(data){
    if(typeof data ==='array'){
      data.forEach(x=>{
        if(x.length>40){
          return true
        }
      })
      return false
    }else{
      if(data.length>40){
        return true
      }else{
        return false
      }
    }
  },//input(40true)
  /**/
  error(text) {
    Notification.error({
      message: text,
      offset: 300,
      duration:1000
    })
  },//(,)
  success(text) {
    Notification.success({
      message: text,
      offset: 300,
      duration:1000
    })
  },//()
  warning(text) {
    Notification.warning({
      message: text,
      offset: 300,
      duration:1000
    })
  },//,()
/**/
  getCity(data){
    let arr = [];
    for (let i = 0; i < data.length; i++) {
      let obj = {};
      obj.label = data[i].area_title;
      obj.value = data[i].area_id;
      arr.push(obj);
    }
    return arr
  },//
  getScale(data){
    let arr = [];
    for (let i = 0; i < data.length; i++) {
      let obj = {};
      obj.label = data[i].scale_money;
      obj.value = data[i].scale_id;
      arr.push(obj)
    }
    return arr
  },//
  getStage(data){
    let arr = [];
    for (let i = 0; i < data.length; i++) {
      let obj = {};
      obj.label = data[i].stage_name;
      obj.value = data[i].stage_id;
      arr.push(obj)
    }
    return arr
  },//
  getIndustry(data){
    let arr = [];
    for (let i = 0; i < data.length; i++) {
      let obj = {};
      obj.label = data[i].industry_name;
      obj.value = data[i].industry_id;
      arr.push(obj)
    }
    return arr
  },//







  console(text) {
    let type=true;//true,false
    if(type) console.log(text);
  },//console

}

export default{
  install(Vue){
    Vue.prototype.$tool = tool;
  }
}



